.left-hover {
  width: 50%;
  height: 100%;
  background-color: rgba(34, 49, 63, 0.8);
  position: absolute;
  top: 0;
  overflow: hidden;
  opacity: 0.8;
  -webkit-animation-name: hover;
          animation-name: hover;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
}

@-webkit-keyframes hover {
  from {
    width: 0;
  }
  to {
    width: 50%;
  }
}

@keyframes hover {
  from {
    width: 0;
  }
  to {
    width: 50%;
  }
}

.hover-text {
  position: absolute;
  top: 10rem;
  left: 15rem;
  font-size: 0rem;
  color: #ffffff;

  letter-spacing: 0.2rem;
  word-spacing: 0.4rem;
  font-weight: 100;
  text-transform: uppercase;
  -webkit-animation-name: increase1;
          animation-name: increase1;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.heading-2 {
  margin-top: 3rem;
  color: lightskyblue;
}

@-webkit-keyframes increase1 {
  from {
    font-size: 0rem;
  }
  to {
    font-size: 1.3rem;
  }
}

@keyframes increase1 {
  from {
    font-size: 0rem;
  }
  to {
    font-size: 1.3rem;
  }
}

.list-btn-2 {
  position: absolute;
  top: 16rem;
  left: 4rem;
}

.btn-item-2 {
  color: #f0f0f0;
  background-color: #d8202b;
  box-shadow: none;
  border: none;
  cursor: pointer;
  padding: 1rem 2rem;
  font-size: 1.3rem;
  font-weight: bold;
  -webkit-animation-name: name;
          animation-name: name;
  -webkit-animation-duration: 4s ease-out;
          animation-duration: 4s ease-out;
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-transform: scale(0);
          transform: scale(0);
  border-radius: 0.5rem;
  margin-top: 2.5rem;
}
@-webkit-keyframes name {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes name {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
